<template>
    <button
      @click="buttonClick"
      class="long-button">
        {{ buttonData.name }}
    </button>
</template>

<script>
import { mapActions } from 'vuex'

export default {
    name: 'LongButton',
    props: {
        buttonData: {}
    },
    methods: {
      ...mapActions([
        "doAction"
      ]),
      buttonClick(){
        this.doAction(this.buttonData.action)
      }
    },
}
</script>

<style>
  .long-button{
    width: 640px;
    height: 50px;
  }
</style>