<template>
    <div>
        <div>
          <div
            v-for="answer in questionsQR.answers"
            :key="answer.id"
          >
          {{answer.name}} : {{ answerVotes(answer.id) }} голосов

          </div>
        </div>
    </div>
</template>
  
<script>
import { mapGetters } from 'vuex'

export default {
    name: 'ChartQR',
    data() {
      return {
        sessionInfo: {}
      }
    },
    computed: {
      ...mapGetters([
          "sessionQR",
          "questionsQR"
      ])
    },
    methods: {
        answerVotes(answerId){
          const answerVotes = this.sessionInfo.result?.find( e => e.id === answerId)
          if (answerVotes) {
            return answerVotes.votes
          }
          
        },
               
        async getVotes(session) {
          try {
            const rawResponse = await fetch(
              `https://pitech.me/medicine/api/v1/voting/results?id=${session}`,
              {
                method: "GET",
                // mode: "no-cors",
                headers: {
                  Accept: "application/json",
                  "Content-Type": "application/json",
                },
              }
            );
            return await rawResponse.json();
          } catch (e) {
            return { error: 1 };
          }
        },

        async getSessionInfo(){
          const votes = await this.getVotes(this.sessionQR)
          this.sessionInfo = votes
          // console.log(votes);
        }
      
    },
    mounted() {
        
      this.getSessionInfo()

    }
}
</script>
  
